body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: linear-gradient(84deg, hsla(30, 97%, 54%, 0.6), hsla(17, 100%, 50%, 0.6), hsla(358, 71%, 44%, 0.6), hsla(291, 100%, 58%, 0.6)), url(../src/pexels-david-bartus-435840.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

label {
    font-size: 22px;
}

h1 {
    position: relative;
    z-index: 0;
    display: inline-block;
    width: auto;
    margin: 0;
    font-size: 5em;
    margin-bottom: 40px;
    font-variant: small-caps;
    font-family: 'Fugaz one', cursive;
}

h1 span {
    animation: animate 1s linear infinite;
}

h1 span:nth-child(1) {
    animation-delay: 0s;
}

h1 span:nth-child(2) {
    animation-delay: 0.2s;
}

h1 span:nth-child(3) {
    animation-delay: 0.4s;
}

h1 span:nth-child(4) {
    animation-delay: 0.6s;
}

h1 span:nth-child(5) {
    animation-delay: 0.8s;
}

@keyframes animate {
    0%,
    80% {
        columns: #333333;
        text-shadow: none;
    }
    100% {
        color: #fff;
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #fff, 0 0 80px #fff, 0 0 120px #fff, 0 0 160px #fff;
    }
}

form.card {
    border: none;
    background: #000;
}

.letra {
    color: white;
    white-space: pre-wrap;
}

.card {
    -webkit-box-shadow: 2px 21px 30px -8px rgba(0, 0, 0, 0.85);
    -moz-box-shadow: 2px 21px 30px -8px rgba(0, 0, 0, 0.85);
    box-shadow: 2px 21px 30px -8px rgba(0, 0, 0, 0.85);
}

.card img {
    width: 100%;
    margin-bottom: 1rem;
    border-radius: 1em;
}

h2 {
    margin: 2rem 0;
    font-family: sans-serif;
    border-bottom: 3px solid #df3551;
    padding-bottom: 10px;
}

.card-body i {
    font-size: 3rem;
    margin-right: 3rem;
}

.fa-facebook {
    color: #3b5998;
}

.fa-twitter {
    color: #0084b4;
}

.fa-lastfm {
    color: #d51007;
}